/* Imports
-------------------------*/
@import "bourbon/bourbon";
@import "neat/neat";

//@import "partials/product-type";

/* Variables
-----------------------------*/
$green: #3c6b27;
$brown: #2E2822;
$white: #ffffff;
$accent: #D9DBA6;


/* Utility Classes
-------------------------*/
.transparent {
    background-color: transparent !important;
}
.green {
    background-color: $green;
    a {
        //color: $white !important;
        &:hover {
           // color: $accent !important;
        }
    }
}

/* # HTML5 Reset
---------------------------------------------------------------------------------------------------- */

/* ## Baseline Normalize
--------------------------------------------- */
/* normalize.css v3.0.1 | MIT License | git.io/normalize */

