/* ## Box Sizing
--------------------------------------------- */

html,
input[type="search"]{
    -webkit-box-sizing: border-box;
    -moz-box-sizing:    border-box;
    box-sizing:         border-box;
}

*,
*:before,
*:after {
    box-sizing: inherit;
}


/* ## Float Clearing
--------------------------------------------- */

.author-box:before,
.clearfix:before,
.entry:before,
.entry-content:before,
.footer-widgets:before,
.nav-primary:before,
.nav-secondary:before,
.pagination:before,
.site-container:before,
.site-footer:before,
.site-header:before,
.site-inner:before,
.wrap:before {
    content: " ";
    display: table;
}

.author-box:after,
.clearfix:after,
.entry:after,
.entry-content:after,
.footer-widgets:after,
.nav-primary:after,
.nav-secondary:after,
.pagination:after,
.site-container:after,
.site-footer:after,
.site-header:after,
.site-inner:after,
.wrap:after {
    clear: both;
    content: " ";
    display: table;
}


/* # Defaults
---------------------------------------------------------------------------------------------------- */

/* ## Typographical Elements
--------------------------------------------- */

html {
    font-size: 62.5%; /* 10px browser default */
}

/* Chrome fix */
body > div {
    font-size: 1.8rem;
}

body {
    background-color: $white;
    color: $brown;
    font-family: "adelle-sans",sans-serif;
    font-size: 18px;
    font-size: 1.8rem;
    font-weight: 300;
    line-height: 1.625;
    margin: 0;
}

a,
button,
input:focus,
input[type="button"],
input[type="reset"],
input[type="submit"],
textarea:focus,
.button,
.gallery img {
    -webkit-transition: all 0.1s ease-in-out;
    -moz-transition:    all 0.1s ease-in-out;
    -ms-transition:     all 0.1s ease-in-out;
    -o-transition:      all 0.1s ease-in-out;
    transition:         all 0.1s ease-in-out;
}

a {
    color: $accent;
    text-decoration: underline;
}

a:hover,
a:focus {
    color: #333;
    text-decoration: none;
}

p {
    margin: 0 0 28px;
    padding: 0;
}

ol,
ul {
    margin: 0;
    padding: 0;
}

li {
    list-style-type: none;
}

hr {
    border: 0;
    border-collapse: collapse;
    border-top: 1px solid #ddd;
    clear: both;
    margin: 1em 0;
}

b,
strong {
    font-weight: 700;
}

blockquote,
cite,
em,
i {
    font-style: italic;
}

blockquote {
    margin: 40px;
}

blockquote::before {
    content: "\201C";
    display: block;
    font-size: 30px;
    font-size: 3rem;
    height: 0;
    left: -20px;
    position: relative;
    top: -10px;
}


/* ## Headings
--------------------------------------------- */

h1,
h2,
h3,
h4,
h5,
h6 {
    color: $brown;
    font-family: "adelle",serif;
    font-weight: 400;
    line-height: 1.2;
    margin: 0 0 10px;
}

h1 {
    font-size: 36px;
    font-size: 3.6rem;
}

h2 {
    font-size: 30px;
    font-size: 3rem;
}

h3 {
    font-size: 24px;
    font-size: 2.4rem;
}

h4 {
    font-size: 20px;
    font-size: 2rem;
}

h5 {
    font-size: 18px;
    font-size: 1.8rem;
}

h6 {
    font-size: 16px;
    font-size: 1.6rem;
}

/* ## Objects
--------------------------------------------- */

embed,
iframe,
img,
object,
video,
.wp-caption {
    max-width: 100%;
}

img {
    height: auto;
}

.featured-content img,
.gallery img {
    width: auto;
}

/* ## Gallery
--------------------------------------------- */

.gallery {
    overflow: hidden;
}

.gallery-item {
    float: left;
    margin: 0 0 28px;
    text-align: center;
}

.gallery-columns-2 .gallery-item {
    width: 50%;
}

.gallery-columns-3 .gallery-item {
    width: 33%;
}

.gallery-columns-4 .gallery-item {
    width: 25%;
}

.gallery-columns-5 .gallery-item {
    width: 20%;
}

.gallery-columns-6 .gallery-item {
    width: 16.6666%;
}

.gallery-columns-7 .gallery-item {
    width: 14.2857%;
}

.gallery-columns-8 .gallery-item {
    width: 12.5%;
}

.gallery-columns-9 .gallery-item {
    width: 11.1111%;
}

.gallery img {
    border: 1px solid #ddd;
    height: auto;
    padding: 4px;
}

.gallery img:hover,
.gallery img:focus {
    border: 1px solid #999;
}

/* ## Forms
--------------------------------------------- */

input,
select,
textarea {
    background-color: $white;
    border: 1px solid #ddd;
    color: #333;
    font-size: 18px;
    font-size: 1.8rem;
    font-weight: 300;
    padding: 16px;
    width: 100%;
}

input:focus,
textarea:focus {
    border: 1px solid #999;
    outline: none;
}

input[type="checkbox"],
input[type="image"],
input[type="radio"] {
    width: auto;
}

::-moz-placeholder {
    color: #333;
    font-weight: 300;
    opacity: 1;
}

::-webkit-input-placeholder {
    color: #333;
    font-weight: 300;
}

button,
input[type="button"],
input[type="reset"],
input[type="submit"],
.button {
    background-color: $accent;
    border: 2px solid $green;
    color: $green;
    cursor: pointer;
    font-size: 16px;
    font-size: 1.6rem;
    font-weight: 300;
    padding: 14px 28px;
    text-transform: uppercase;
    width: auto;
    border-radius: 16px;

    &:hover,
    &:focus{
        background-color: darken($accent, 20%);
    }

}




.woocommerce #respond input#submit,
.woocommerce a.button,
.woocommerce button.button,
.woocommerce input.button {
    background-color: $accent !important;
    border: 2px solid $green  !important;
    color: $green  !important;
    cursor: pointer;
    font-size: 16px  !important;
    font-size: 1.6rem  !important;
    font-weight: 300  !important;
    padding: 14px 28px  !important;
    text-transform: uppercase !important;
    width: auto !important;
    border-radius: 16px !important;

    &:hover,
    &:focus{
        background-color: darken($accent, 20%) !important;
    }

}


.woocommerce div.product p.price, .woocommerce div.product span.price {
    color: #77a464;
    font-size: 1.25em;
    letter-spacing: 3px;
    font-weight: 700;
}

.entry-content .button:hover,
.entry-content .button:focus {

}

.footer-widgets button,
.footer-widgets input[type="button"],
.footer-widgets input[type="reset"],
.footer-widgets input[type="submit"],
.footer-widgets .button {
    @extend button:hover;
}

.footer-widgets button:hover,
.footer-widgets input:hover[type="button"],
.footer-widgets input:hover[type="reset"],
.footer-widgets input:hover[type="submit"],
.footer-widgets .button:hover,
.footer-widgets button:focus,
.footer-widgets input:focus[type="button"],
.footer-widgets input:focus[type="reset"],
.footer-widgets input:focus[type="submit"],
.footer-widgets .button:focus {
    background-color: $white;
    color: #333;
}

.button {
    display: inline-block;
}

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button {
    display: none;
}

/* ## Tables
--------------------------------------------- */

table {
    border-collapse: collapse;
    border-spacing: 0;
    line-height: 2;
    margin-bottom: 40px;
    width: 100%;
}

tbody {
    border-bottom: 1px solid #ddd;
}

td,
th {
    text-align: left;
}

td {
    border-top: 1px solid #ddd;
    padding: 6px 0;
}

th {
    font-weight: 400;
}

/* ## Screen Reader Text
--------------------------------------------- */

.screen-reader-text,
.screen-reader-text span,
.screen-reader-shortcut {
    position: absolute !important;
    clip: rect(0, 0, 0, 0);
    height: 1px;
    width: 1px;
    border: 0;
    overflow: hidden;
}

.screen-reader-text:focus,
.screen-reader-shortcut:focus,
.genesis-nav-menu .search input[type="submit"]:focus,
.widget_search input[type="submit"]:focus  {
    clip: auto !important;
    height: auto;
    width: auto;
    display: block;
    font-size: 1em;
    font-weight: bold;
    padding: 15px 23px 14px;
    color: #333;
    background: $white;
    z-index: 100000; /* Above WP toolbar. */
    text-decoration: none;
    box-shadow: 0 0 2px 2px rgba(0,0,0,.6);
}

.more-link {
    position: relative;
}


/* # Structure and Layout
---------------------------------------------------------------------------------------------------- */

/* ## Site Containers
--------------------------------------------- */

.site-inner,
.wrap {
    margin: 0 auto;
    max-width: 100%;
}

.site-inner {
    clear: both;
    margin-top: 180px;
}

/* ## Column Widths and Positions
--------------------------------------------- */

/* ### Wrapping div for .content and .sidebar-primary */

.content-sidebar-sidebar .content-sidebar-wrap,
.sidebar-content-sidebar .content-sidebar-wrap,
.sidebar-sidebar-content .content-sidebar-wrap {
    width: 980px;
}

.content-sidebar-sidebar .content-sidebar-wrap {
    float: left;
}

.sidebar-content-sidebar .content-sidebar-wrap,
.sidebar-sidebar-content .content-sidebar-wrap {
    float: right;
}

/* ### Content */

.content {
    width: 800px;
}

.content-sidebar .content,
.content-sidebar-sidebar .content,
.sidebar-content-sidebar .content {
    float: left;
}

.content-sidebar-sidebar .content,
.sidebar-content-sidebar .content,
.sidebar-sidebar-content .content {
    width: 580px;
}

.full-width-content .content {
    width: 80%;
    margin: 0 auto;
}
.home .content {
    width: 100%;
    .front-page-2 {
        .widget-area {
            @include grid-container();
        }
        .widget {
            @include grid-column(4);
        }
    }
}


/* ### Primary Sidebar  */

.sidebar-primary {
    float: right;
    width: 360px;
}

.sidebar-content .sidebar-primary,
.sidebar-sidebar-content .sidebar-primary {
    float: left;
}

/* ### Secondary Sidebar */

.sidebar-secondary {
    float: left;
    width: 180px;
}

.content-sidebar-sidebar .sidebar-secondary {
    float: right;
}

/* ## Column Classes
--------------------------------------------- */
/* Link: http://twitter.github.io/bootstrap/assets/css/bootstrap-responsive.css */

.five-sixths,
.four-sixths,
.one-fourth,
.one-half,
.one-sixth,
.one-third,
.three-fourths,
.three-sixths,
.two-fourths,
.two-sixths,
.two-thirds {
    float: left;
    margin-left: 2.564102564102564%;
}

.one-half,
.three-sixths,
.two-fourths {
    width: 48.717948717948715%;
}

.one-third,
.two-sixths {
    width: 31.623931623931625%;
}

.four-sixths,
.two-thirds {
    width: 65.81196581196582%;
}

.one-fourth {
    width: 23.076923076923077%;
}

.three-fourths {
    width: 74.35897435897436%;
}

.one-sixth {
    width: 14.52991452991453%;
}

.five-sixths {
    width: 82.90598290598291%;
}

.first {
    clear: both;
    margin-left: 0;
}


/* # Common Classes
---------------------------------------------------------------------------------------------------- */

/* ## Avatar
--------------------------------------------- */

.avatar {
    float: left;
}

.alignleft .avatar,
.author-box .avatar {
    margin-right: 24px;
}

.alignright .avatar {
    margin-left: 24px;
}

.comment .avatar {
    margin: 0 16px 24px 0;
}

/* ## Genesis
--------------------------------------------- */

.breadcrumb {
    margin-bottom: 20px;
}

.archive-description,
.author-box {
    background-color: $white;
    font-size: 16px;
    font-size: 1.6rem;
    margin-bottom: 40px;
    padding: 40px;
}

.author-box-title {
    font-size: 16px;
    font-size: 1.6rem;
    margin-bottom: 4px;
}

.archive-description p:last-child,
.author-box p:last-child {
    margin-bottom: 0;
}

/* ## Search Form
--------------------------------------------- */

.search-form {
    overflow: hidden;
    input[type=search] {
        font-style: 14px;
        font-size: 1.4rem;
        padding: 10px;
    }
}

.site-header .search-form {
    margin-top: 4px;
}

.entry-content .search-form,
.site-header .search-form {
}

.genesis-nav-menu .search input[type="submit"],
.widget_search input[type="submit"] {
    border: 0;
    clip: rect(0, 0, 0, 0);
    height: 1px;
    margin: -1px;
    padding: 0;
    position: absolute;
    width: 1px;
}

/* ## Titles
--------------------------------------------- */

.archive-title {
    font-size: 20px;
    font-size: 2rem;
}

.entry-title {
    font-size: 36px;
    font-size: 3.6rem;
}

.entry-title a,
.sidebar .widget-title a {
    color: #333;
}

.entry-title a:hover,
.entry-title a:focus {
    color: #c3251d;
}

.widget-title {
    font-size: 18px;
    font-size: 1.8rem;
    margin-bottom: 20px;
}

.footer-widgets .widget-title {
    color: $white;
}

/* ## WordPress
--------------------------------------------- */

a.aligncenter img {
    display: block;
    margin: 0 auto;
}

a.alignnone {
    display: inline-block;
}

.alignleft {
    float: left;
    text-align: left;
}

.alignright {
    float: right;
    text-align: right;
}

a.alignleft,
a.alignnone,
a.alignright {
    max-width: 100%;
}

img.centered,
.aligncenter {
    display: block;
    margin: 0 auto 24px;
}

img.alignnone,
.alignnone {
    margin-bottom: 12px;
}

a.alignleft,
img.alignleft,
.wp-caption.alignleft {
    margin: 0 24px 24px 0;
}

a.alignright,
img.alignright,
.wp-caption.alignright {
    margin: 0 0 24px 24px;
}

.wp-caption-text {
    font-size: 14px;
    font-size: 1.4rem;
    font-weight: 700;
    text-align: center;
}

.entry-content p.wp-caption-text {
    margin-bottom: 0;
}

.sticky {
}

.entry-content .wp-audio-shortcode,
.entry-content .wp-playlist,
.entry-content .wp-video {
    margin: 0 0 28px;
}


/* # Widgets
---------------------------------------------------------------------------------------------------- */

.widget {
    word-wrap: break-word;
}

.widget ol > li {
    list-style-position: inside;
    list-style-type: decimal;
    padding-left: 20px;
    text-indent: -20px;
}

.widget li li {
    border: 0;
    margin: 0 0 0 30px;
    padding: 0;
}

.widget_calendar table {
    width: 100%;
}

.widget_calendar td,
.widget_calendar th {
    text-align: center;
}

/* ## Featured Content
--------------------------------------------- */

.featured-content .entry {
    border-bottom: 2px solid #f5f5f5;
    margin-bottom: 20px;
    padding: 0 0 24px;
}

.footer-widgets .entry {
    border-bottom: 1px dotted #666;
}

.featured-content .entry-title {
    font-size: 20px;
    font-size: 2rem;
}


/* # Plugins
---------------------------------------------------------------------------------------------------- */

/* ## Genesis eNews Extended
--------------------------------------------- */

.enews-widget,
.enews-widget .widget-title {
    color: $white;
}

.sidebar .widget.enews-widget {
    background-color: #333;
}

.enews-widget input,
.enews-widget input:focus {
    border: 1px solid #333;
}

.enews-widget input {
    font-size: 16px;
    font-size: 1.6rem;
    margin-bottom: 16px;
}

.enews-widget input[type="submit"] {
    background-color: #c3251d;
    color: $white;
    margin: 0;
    width: 100%;
}

.enews-widget input:hover[type="submit"],
.enews-widget input:focus[type="submit"]  {
    background-color: $white;
    color: #333;
}

.enews form + p {
    margin-top: 24px;
}

/* ## Jetpack
--------------------------------------------- */

#wpstats {
    display: none;
}


/* # Skip Links
---------------------------------------------------------------------------------------------------- */
.genesis-skip-link {
    margin: 0;
}

.genesis-skip-link li {
    height: 0;
    width: 0;
    list-style: none;
}

/* Display outline on focus */
:focus {
    color: #333;
    outline: #ccc solid 1px;
}


/* # Site Header
---------------------------------------------------------------------------------------------------- */

.site-header {
    background-color: $accent;
    min-height: 160px;
}

.site-header .wrap {
    padding: 40px 0;
}

.site-header .wrap {
    padding: 0;
}

.header {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 999;
}
.admin-bar .header {
    top: 32px;
}

.site-header {
    //padding: 2.5% 0;
    min-height: 0;
}

.header.shrink {
    -webkit-box-shadow: 0 1px rgba(34, 34, 34, 0.1);
    -moz-box-shadow:    0 1px rgba(34, 34, 34, 0.1);
    box-shadow:         0 1px rgba(34, 34, 34, 0.1);
}

.header.shrink .site-header {
    padding: 0;
    -webkit-transition: all 0.7s ease-in-out;
    -moz-transition:    all 0.7s ease-in-out;
    -ms-transition:     all 0.7s ease-in-out;
    -o-transition:      all 0.7s ease-in-out;
    transition:         all 0.7s ease-in-out;
}

.site-inner,
.shrink .site-title > a {
    -webkit-transition: all 0.7s ease-in-out;
    -moz-transition:    all 0.7s ease-in-out;
    -ms-transition:     all 0.7s ease-in-out;
    -o-transition:      all 0.7s ease-in-out;
    transition:         all 0.7s ease-in-out;
}



/* ## Title Area
--------------------------------------------- */

.title-area {
    padding: 10px 0;
    @include grid-column(3);
    float: left;
}

.header-full-width .title-area {
    width: 100%;
}

.site-title {
    font-size: 32px;
    font-size: 3.2rem;
    font-weight: 400;
    line-height: 1.2;
}

.site-title a,
.site-title a:hover,
.site-title a:focus {
    color: #333;
}

.header-image .site-title > a {
    background: url(images/logo@2x.png) no-repeat center $accent;
    background-size: contain;
    float: left;
    height: 100px;
    width: 100%;
    padding-top: 10px;
}
.shrink .site-title > a {
    background: url(images/logo@2x.png) no-repeat center;
    transform: scale(0.9);
    background-size: 100%;
}

.site-description {
    font-size: 16px;
    font-size: 1.6rem;
    font-weight: 300;
    line-height: 1.5;
}

.site-description,
.site-title {
    margin-bottom: 0;
}

